// Call plugin.
import { callActions as actions } from '../../interface/actions'
import * as eventTypes from '../../interface/eventTypes'
import { OP_TRANSITIONS, OP_STATUS } from '../../interface/constants'

// Call Reports.
import { REPORTER_OPERATION_EVENTS_MAP } from '../reporter/constants'

/*
 * Factory for an "operation tracker".
 * An "operation tracker" is a utility object for managing call state updates
 *    and events for operation tracking.
 *
 * Each function is a code-block that was repeated numerous times in the
 *    CallManager. So it was split out to reduce noise.
 * The fact that this utility exists is a sign that how we do this is bad.
 */
export default function createTracker(container, callId, type, isLocal) {
  const tracker = {
    status: undefined
  }

  tracker.start = () => {
    if (tracker.status) {
      throw new Error('Operation tracking has already been started.')
    }

    tracker.opEvent = startOperation(container, callId, type, isLocal)
    tracker.status = OP_STATUS.ONGOING
    return tracker.opEvent
  }

  tracker.update = result => {
    if (tracker.status !== OP_STATUS.ONGOING) {
      throw new Error('Operation tracking cannot be updated.')
    }
    updateOperation(container, tracker.opEvent, callId, isLocal, result)
    tracker.status = OP_STATUS.PENDING
  }

  tracker.finish = err => {
    if (![OP_STATUS.ONGOING, OP_STATUS.PENDING].includes(tracker.status)) {
      throw new Error('Operation tracking cannot be finished.')
    }

    finishOperation(container, tracker.opEvent, callId, isLocal, err)
    tracker.status = 'FINISHED'
  }

  return tracker
}

export function startOperation(container, callId, type, isLocal) {
  const { context, CallReporter, emitEvent } = container

  const callReport = CallReporter.getReport(callId)
  const opEvent = callReport.addEvent(REPORTER_OPERATION_EVENTS_MAP[type])

  // Update call state to indicate that an operation is starting.
  context.dispatch(
    actions.operationUpdate(callId, type, isLocal, {
      transition: OP_TRANSITIONS.START,
      eventId: opEvent.id
    })
  )

  // Emit "operation start" event for the call.
  emitEvent(eventTypes.CALL_OPERATION, {
    callId,
    isLocal,
    operation: type,
    transition: OP_TRANSITIONS.START,
    previous: undefined
  })

  return opEvent
}

export function updateOperation(container, opEvent, callId, isLocal, result) {
  const { context, emitEvent } = container

  context.dispatch(
    actions.operationUpdate(callId, opEvent.type, isLocal, {
      transition: OP_TRANSITIONS.UPDATE,
      operationData: result && result.operationData ? result.operationData : undefined,
      eventId: opEvent.id
    })
  )

  // Emit "operation update/finish" event for the call.
  emitEvent(eventTypes.CALL_OPERATION, {
    callId,
    isLocal,
    operation: opEvent.type,
    transition: OP_TRANSITIONS.UPDATE,
    previous: {
      operation: opEvent.type,
      status: OP_STATUS.ONGOING
    }
  })
}

export function finishOperation(container, opEvent, callId, isLocal, err) {
  const { context, emitEvent } = container

  // Update call state to indicate the operation is finished, since it failed.
  context.dispatch(
    actions.operationUpdate(callId, opEvent.type, isLocal, {
      transition: OP_TRANSITIONS.FINISH,
      eventId: opEvent.id
    })
  )

  // Emit "operation finish" event for the application.
  emitEvent(eventTypes.CALL_OPERATION, {
    callId,
    isLocal,
    operation: opEvent.type,
    transition: OP_TRANSITIONS.FINISH,
    error: err,
    previous: {
      operation: opEvent.type,
      status: OP_STATUS.ONGOING
    }
  })
}
